@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideFadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-30px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideFadeInRight {
  0% {
    opacity: 0;
    transform: translateX(30px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes bounceDown {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }
  60% {
    opacity: 1;
    transform: translateY(10px);
  }
  80% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes bounceRight {
  0% {
    opacity: 0;
    transform: translateX(40px);
  }
  60% {
    opacity: 1;
    transform: translateX(-10px);
  }
  80% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes splashDrop {
  0% {
    transform: translateY(-300px) scale(1) var(--initial-transform);
    opacity: 0;
  }
  80% {
    transform: translateY(0px) scale(1.1) var(--initial-transform);
    opacity: var(--final-opacity, 1);
  }
  100% {
    transform: translateY(-10px) scale(1) var(--initial-transform);
    opacity: var(--final-opacity, 1);
  }
}

@keyframes float {
  0% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0); }
}

@keyframes cardShuffleIn {
  0% {
    opacity: 0;
    transform: translateY(-100px) rotate(-10deg) scale(0.95);
  }

  60% {
    opacity: 1;
    transform: translateY(10px) rotate(3deg) scale(1.03);
  }

  100% {
    opacity: 1;
    transform: translateY(0) rotate(0deg) scale(1);
  }
}

@keyframes flipPillRise {
  0% {
    transform: rotateX(60deg) translateY(50px);
    opacity: 0;
  }
  60% {
    transform: rotateX(-10deg) translateY(-10px);
    opacity: 1;
  }
  80% {
    transform: rotateX(5deg) translateY(2px);
  }
  100% {
    transform: rotateX(0deg) translateY(0);
  }
}

@keyframes slideBounceInLeft {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }
  60% {
    opacity: 1;
    transform: translateX(20px); 
  }
  80% {
    transform: translateX(-10px); 
  }
  100% {
    transform: translateX(0); 
  }
}
